body.single-services.multi-day-charters .charter-time_single--service {
  display: none;
}

.page-content {
  background-color: #fff;
  padding: 80px 0;
}

article.page-content {
  margin-top: 104px;
}

.single-content {
  margin-top: 104px;
}

.single-services .order-sidebar .charter-input.charter-time {
  border-top: 0;
}

.single-services .order-sidebar .charter-time_multy {
  border-top: 0;
}

.single-product .order-sidebar .charter-input.charter-passenger {
  border-top: 0;
}

.fleet-content .container {
  position: static;
}

.fleet-content .container .col-md-12 {
  position: static;
}

.search-title {
  font-size: 36px;
  line-height: 50px;
  margin-bottom: 8px;
  text-transform: capitalize;
}

.page-content_titles h4 {
  font-size: 36px;
  line-height: 36px;
  margin-bottom: 8px;
}

.page-content_titles p {
  font-size: 17px;
  color: #797C84;
  line-height: 26px;
  margin-bottom: 55px;
  max-width: 570px;
}

.page-content_wrap {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 25px;
  margin-bottom: 80px;
  border-bottom: 2px solid #E9E9E9;
}

.service-item {
  width: 32%;
  max-width: 370px;
  margin-bottom: 55px;
  margin-right: 30px;
}

.service-item:nth-child(3n) {
  margin-right: 0;
}

.service-item:hover img {
  transform: scale(1.1);
}

.service-item .service-item_thumb {
  display: block;
  width: 100%;
  height: 320px;
  margin-bottom: 15px;
  border-radius: 4px;
  overflow: hidden;
}

.service-item .service-item_thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.service-item .service-item_description h4 {
  margin-bottom: 4px;
}

.service-item .service-item_description h4 a {
  font-size: 22px;
  font-weight: 500;
  color: #1F283C;
  line-height: 30px;
}

.service-item .service-item_description p {
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 12px;
}

.service-item .service-item_description .service-item_button {
  display: block;
  width: 115px;
  text-align: center;
  padding: 10px 0;
  background-color: #F6F6F6;
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
  color: #1F283C;
  border-radius: 12px;
  transition: .3s all ease;
}

.service-item .service-item_description .service-item_button:hover {
  background-color: #dddddd;
}

@media screen and (max-width: 1200px) {
  .service-item {
    max-width: 290px;
  }
}

@media screen and (max-width: 990px) {
  .page-content_wrap {
    justify-content: space-between;
  }
  .service-item {
    max-width: 100%;
    width: 48%;
    margin-right: 0;
  }
}

@media screen and (max-width: 500px) {
  .page-content {
    padding: 25px 0;
  }
  article.page-content {
    margin-top: 79px;
  }
  .single-content {
    margin-top: 79px;
  }
  .page-content_titles h4 {
    font-size: 26px;
    line-height: 26px;
  }
  .page-content_titles p {
    font-size: 16px;
    line-height: 23px;
    margin-bottom: 25px;
  }
  .page-content_wrap {
    padding-bottom: 0;
    margin-bottom: 30px;
  }
  .service-item {
    width: 100%;
    margin-bottom: 30px;
  }
  .service-item .service-item_thumb {
    height: 290px;
  }
  .service-item .service-item_description h4 a {
    font-size: 21px;
  }
  .service-item .service-item_description p {
    font-size: 14px;
  }
  .service-item .service-item_description .service-item_button {
    font-size: 14px;
  }
}

